/* -------- BUTTTTTTTONS------*/
button,
input,
.btn,
a.btn {
  -webkit-font-smoothing: antialiased;
  text-rendering: optimizelegibility;
  color: white;
}

.btn {
  border: none;
  font-family: inherit;
  font-size: inherit;
  color: inherit;
  background: none;
  cursor: pointer;
  padding: 25px 80px;
  display: inline-block;
  margin: 15px 30px;
  text-transform: uppercase;
  letter-spacing: 1px;
  font-weight: 700;
  outline: none;
  position: relative;
  -webkit-transition: all 0.3s cubic-bezier(0.84, 0.01, 0.19, 0.93);
  -moz-transition: all 0.3s cubic-bezier(0.84, 0.01, 0.19, 0.93);
  transition: all 0.3s cubic-bezier(0.84, 0.01, 0.19, 0.93);
}

.btn:after {
  content: '';
  position: absolute;
  z-index: -1;
  -webkit-transition: all 0.3s cubic-bezier(0.84, 0.01, 0.19, 0.93);
  -moz-transition: all 0.3s cubic-bezier(0.84, 0.01, 0.19, 0.93);
  transition: all 0.3s cubic-bezier(0.84, 0.01, 0.19, 0.93);
}

.btn:before {
  font-family: 'icomoon';
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;
  position: relative;
  -webkit-font-smoothing: antialiased;
}

.icon-envelope:before {
  content: "\e945";
}

.icon-profile:before {
  content: "\e923";
}

.icon-arrow-right:before {
  content: "\ea34";
}

.icon-arrow-left:before {
  content: "\ea38";
}

/* Button 7 */
.btn-7 {
  background: transparent !important;
  box-shadow: 0 6px transparent;
  color: #fff;
  border-radius: 3px;
  padding: 20px 60px 20px 90px;
  top: 0px;
}

/* Button 7a */
.btn-7a {
  overflow: hidden;
}

.btn-7a:before {
  position: absolute;
  left: 0;
  width: 40%;
  font-size: 160%;
  line-height: 0.8;
  color: #fff;
}

.btn-7a:hover {
  background: transparent !important;
  box-shadow: 0 4px transparent;
  top: 2px;
}

.btn-7a.btn-activated {
  -webkit-animation: fadeOutText 0.5s;
  -moz-animation: fadeOutText 0.5s;
  animation: fadeOutText 0.5s;
}

.btn-7a.btn-activated:before {
  -webkit-animation: moveToRight 0.5s;
  -moz-animation: moveToRight 0.5s;
  animation: moveToRight 0.5s;
}

@-webkit-keyframes fadeOutText {
  0% {
    color: transparent;
  }
  80% {
    color: transparent;
  }
  100% {
    color: #fff;
  }
}

@-moz-keyframes fadeOutText {
  0% {
    color: transparent;
  }
  80% {
    color: transparent;
  }
  100% {
    color: #fff;
  }
}

@keyframes fadeOutText {
  0% {
    color: transparent;
  }
  80% {
    color: transparent;
  }
  100% {
    color: #fff;
  }
}

@-webkit-keyframes moveToRight {
  80% {
    -webkit-transform: translateX(250%);
  }
  81% {
    opacity: 1;
    -webkit-transform: translateX(250%);
  }
  82% {
    opacity: 0;
    -webkit-transform: translateX(250%);
  }
  83% {
    opacity: 0;
    -webkit-transform: translateX(-50%);
  }
  84% {
    opacity: 1;
    -webkit-transform: translateX(-50%);
  }
  100% {
    -webkit-transform: translateX(0%);
  }
}

@-moz-keyframes moveToRight {
  80% {
    -moz-transform: translateX(250%);
  }
  81% {
    opacity: 1;
    -moz-transform: translateX(250%);
  }
  82% {
    opacity: 0;
    -moz-transform: translateX(250%);
  }
  83% {
    opacity: 0;
    -moz-transform: translateX(-50%);
  }
  84% {
    opacity: 1;
    -moz-transform: translateX(-50%);
  }
  100% {
    -moz-transform: translateX(0%);
  }
}

@keyframes moveToRight {
  80% {
    transform: translateX(250%);
  }
  81% {
    opacity: 1;
    transform: translateX(250%);
  }
  82% {
    opacity: 0;
    transform: translateX(250%);
  }
  83% {
    opacity: 0;
    transform: translateX(-50%);
  }
  84% {
    opacity: 1;
    transform: translateX(-50%);
  }
  100% {
    transform: translateX(0%);
  }
}

