}

.accordion {
  position: relative;
  background-color: #eee;
}

.container {
  width: 100%;
  padding: 0 !important;
}

.accordionTitle,
.accordion__Heading {
  background-color: #E25257;
  text-align: center;
  font-weight: 700;
  padding: 2em;
  display: block;
  text-decoration: none;
  color: #fff;
  transition: background-color 0.5s ease-in-out;
}

.accordionTitle:before,
.accordion__Heading:before {
  content: "+";
  font-size: 1.5em;
  line-height: 0.5em;
  float: left;
  transition: transform 0.3s ease-in-out;
}

