article {
  background-color: white;
  position: relative;
  color: black;
  z-index: 1000;
  padding-bottom: 50px;

  iframe {
    width: 100vw;
    margin-top: 50px;
    //// I did this to fix a problem on the game jam side projects page.
  }

  .entry-content.blog-article {

    ol {
      margin: 30px 25vw 0 25vw;

      counter-reset: li;

      li {
        margin-bottom: 20px;
        list-style: none;


        a {
          vertical-align: text-bottom;
        }

        &::before {
          content: counter(li);
          counter-increment: li;
          z-index: -1;
          position: relative;
          color: #fff;
          background: black;
          font-family: $brushFont;
          font-size: 1.5em;
          text-align: center;
          margin-left: -1.3em;
          left: -25px;
          line-height: 27px;
          padding: 0 5px 0 5px;
          //min-width: 35px;
          //display: inline-flex;
          //justify-content: center;
          //align-items: center;

        }
      }
    }

    blockquote {


      p {
        margin-bottom: 10px;
      }
    }

    figure {
      padding: 0;
      max-width: 100vw;
      height: auto;

      &.pullinmore {
        margin-right: 20vw;
      }

      img {
        max-width: 100%;
      }

      a {
        max-width: 100%;

        img {
          max-width: 100%;
        }

      }
    }

    div.sketchfab-embed-wrapper {

      margin-top: 50px;


    }

    ////////////////////////////////////
    /// THIS IS THE PARAGRAPH TEXT FOR
    /// BLOGS IT IS IMPORTANT CONTENT
    //
    p {

      color: black;
      margin: 30px 20vw 0 20vw;
      z-index: 1000;
      padding: 0 5px 0 5px;
      line-height: 1.6;



      @include breakpoint(phone) {
        margin: 20px 10vw 0 10vw;
        padding: 0 5px 0 5px !important;

        img {
          margin-left: -10vw !important;
          width: 100vw !important;
          height: auto !important;
          float: none !important;
          display: block !important;
          margin-bottom: 10px !important;

        }
      }

      iframe {
        padding: 0;
        margin-bottom: 75px;
      }

      img {

        max-height: 90vh;
        margin-top: 20px;
        margin-bottom: 20px;

        &.alignright {
          margin-right: -15vw;
          max-width: 35vmax;
          height: auto;

        }

        &.alignleft {
          margin-left: -15vw;
          max-width: 35vmax;
          height: auto;
        }

        /// end of align left


      }

      ///// end of images

    }

    //// End of p

    pre.wp-block-code {
      margin: 30px 17vw 0 17vw;

      code {
        padding: 1.5em;
        font-size: 1.1em;
        white-space: pre-wrap;
        text-indent: 20px;
        font-family: $monospaced !important;
        word-break: break-all;

        span {
          font-family: $monospaced !important;
        }
      }
    }

    cite {
      margin: 5px 2vw 5px 20vw;
    }

  }

  /// End of entry content


}

