//                  YOUTUBE BETTER OPTIMIZATIONS
////////

// is-active
#ytplayer {
  opacity: 0;
  transition: opacity 0.3s $defaultAni;

  &.is-active {
    opacity: 1;
    transition: opacity 0.3s $defaultAni;
  }

}



// Panel
section.hero-section {
  background-color: #040319;
  position: relative;
  height: 60vh;
  display: flex;
  justify-content: center;

  // Panel video
  div.glitch-container {
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    top: calc(-40px + -20vw);
    //display: block;
    margin: 0 auto;
    min-height: calc(160% + 20vw);
    min-width: calc(100% + 25vw);
    pointer-events: none;
    background-repeat: repeat;

    @include breakpoint(phone) {
      top: calc(-350px + -20vw);
      display: block;
      margin: 0 auto;
      min-height: calc(220% + 200px);
      min-width: calc(100% + 100px);
    }


    iframe {
      height: 100%;
      min-height: 100%;
      min-width: 100%;
      overflow: auto;
      position: absolute;
      transition: opacity .5s;
      width: 100%;

    }

  }

}


