//Image gallery for gallery inserts inside wordpress native
div.gallery {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;


  figure.gallery-item {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;


    div.gallery-icon {
      display: flex;
      flex-wrap: wrap;
      justify-content: center;
      align-items: center;

      a {
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
        align-items: center;

        img {}

      }

    }

  }

}



div.gallery-columns-1 {

  max-height: 60vh;

  figure.gallery-item {
    margin: 0;
    max-width: 100%;
    max-height: 60vh;

    div.gallery-icon {
      max-width: 100%;
      max-height: 60vh;

      a {
        max-height: 60vh;
        max-width: 100%;

        img {

          max-height: 60vh;
          max-width: 100%;

        }

      }
    }
  }

}

//// End of columns 1

