ul.columns-2 {
  justify-content: center;
  max-height: 75vh;

  li.blocks-gallery-item {
    margin: 0;
    max-width: 45vw;
    max-height: 75vh;
    width: auto !important;
    flex-grow: 0 !important;

    figure {
      max-width: 100%;
      max-height: 75vh;
      display: flex;
      justify-content: center;
      align-content: center;

      a {
        max-height: 75vh;
        max-width: 100%;

        img {

          max-height: 75vh;
          max-width: 100%;

        }

      }
    }
  }

}

